export default {
    data () {
        const currentDate = this.getDate()
        return {
            searchContent: '',       
            date: currentDate,
        }
    },
    computed: {
        endDate() {
            return this.getDate()
        }
    },
    methods: {  
        handleSearch () {

        },
        bindDateChange(e) {
            this.searchContent = ''
            this.date = e.target.value
            this.getStatisticsList(true)
        },
        getDate(type) {
            const date = new Date();
            let year = date.getFullYear();
            let month = date.getMonth() + 1;
            let day = date.getDate();

            if (type === 'start') {
                year = year - 5;
            } else if (type === 'end') {
                year = year + 2;
            }
            month = month > 9 ? month : '0' + month;;
            day = day > 9 ? day : '0' + day;
            return `${year}-${month}-${day}`;
        },
        getStatisticsList () {

        }
    },
}
